import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import { localStoragedecryp } from "../../../../components/helpers/function";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { postData } from "../../../../config/axios";
import { formatDate, localStorageencryp } from "../../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../../components/helpers/notification.jsx";
import * as moment from "moment-timezone";

const HeadFormLaporanPenjualanBarang = lazy(() =>
  import("./HeadFormLaporanPenjualanBarang")
);

class FormLaporanPenjualanBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanDataPenjualan: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
    };

    postData("penjualan/get/report", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            isLoading: false,
            export: false,
          });
        } else {
          ToastNotification("success", "Laporan Transaksi Penjualan Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            LaporanDataPenjualan: res.data,
          });
        }
      })
      .catch((error) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          isLoading: false,
          export: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">
            Laporan Transaksi Penjualan
          </li>
        </ol>
        <h1 className="page-header">Laporan Transaksi Penjualan </h1>
        <Panel>
          <PanelHeader>Laporan Transaksi Penjualan</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadFormLaporanPenjualanBarang
              export={this.state.export}
              isLoading={this.state.isLoading}
              LaporanDataPenjualan={this.state.LaporanDataPenjualan}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}
export default FormLaporanPenjualanBarang;
